___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body {
    min-height: 100vh;
    background: linear-gradient(135deg, #4203a9, #90bafc);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    position: relative;
}

.container {
    width: 100%;
    max-width: 500px;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 20px;
    padding: 25px;
    backdrop-filter: blur(8px);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
    position: relative;
}

.search-box {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}

input{
    flex: 1;
    padding: 12px;
    border: none;
    border-radius: 25px;
    background: rgba(255, 255, 255, 0.25);
    color: white;
    font-size: 16px;
    outline: none;
    transition: 0.3s;
}

input::placeholder {
    color: rgba(255, 255, 255, 0.7);
}

input:focus {
    background: rgba(255, 255, 255, 0.35);
}

button {
    padding: 12px 20px;
    border: none;
    border-radius: 25px;
    background: #fff;
    color: #4203a9;
    font-weight: 600;
    cursor: pointer;
    transition: 0.3s;
}

button:hover {
    background: #f0f0f0;
}

.weather-info {
    text-align: center;
    display: none;
}

.weather-info.active {
    display: block;
}

.city {
    font-size: 24px;
    margin-bottom: 10px;
}

.temp {
    font-size: 48px;
    font-weight: 600;
    margin: 10px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.weather-icon {
    width: 40px;
    vertical-align: middle;
}

.description {
    font-size: 18px;
    opacity: 0.8;
    text-transform: capitalize;
}

.details {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    margin-top: 20px;
    padding: 15px;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 15px;
}

.detail-item {
    text-align: center;
}

.detail-item span {
    display: block;
    font-size: 14px;
    opacity: 0.7;
}

.error-message {
    color: #fff;
    background: rgba(255, 0, 0, 0.7);
    text-align: center;
    margin-top: 10px;
    padding: 8px;
    border-radius: 10px;
    display: none;
}

.loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 18px;
    background: rgba(0, 0, 0, 0.5);
    padding: 15px 25px;
    border-radius: 15px;
    display: none;
}

@media (max-width: 480px) {
    .container {
        padding: 15px;
    }

    .temp {
        font-size: 36px;
    }

    .search-box {
        flex-direction: column;
    }

    button {
        width: 100%;
    }

    .details {
        grid-template-columns: 1fr;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,kCAAkC;AACtC;;AAEA;IACI,iBAAiB;IACjB,qDAAqD;IACrD,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,qCAAqC;IACrC,mBAAmB;IACnB,aAAa;IACb,0BAA0B;IAC1B,yCAAyC;IACzC,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,OAAO;IACP,aAAa;IACb,YAAY;IACZ,mBAAmB;IACnB,qCAAqC;IACrC,YAAY;IACZ,eAAe;IACf,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,+BAA+B;AACnC;;AAEA;IACI,qCAAqC;AACzC;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,mBAAmB;IACnB,gBAAgB;IAChB,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;IACd,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,WAAW;IACX,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,SAAS;IACT,gBAAgB;IAChB,aAAa;IACb,qCAAqC;IACrC,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,gCAAgC;IAChC,kBAAkB;IAClB,gBAAgB;IAChB,YAAY;IACZ,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,eAAe;IACf,8BAA8B;IAC9B,kBAAkB;IAClB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI;QACI,aAAa;IACjB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,sBAAsB;IAC1B;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,0BAA0B;IAC9B;AACJ","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n    font-family: 'Poppins', sans-serif;\n}\n\nbody {\n    min-height: 100vh;\n    background: linear-gradient(135deg, #4203a9, #90bafc);\n    color: white;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding: 20px;\n    position: relative;\n}\n\n.container {\n    width: 100%;\n    max-width: 500px;\n    background: rgba(255, 255, 255, 0.15);\n    border-radius: 20px;\n    padding: 25px;\n    backdrop-filter: blur(8px);\n    box-shado